.flex {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-evenly;
  .info {
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    button {
      margin-top: 1em;
      width: 20em;
      border: none;
      height: 3em;
      background-color: #5b60ea;
      color: white;
      cursor: pointer;
      transition: .1s background-color ease;
      &:hover {
        background-color: #5157c1;
      }
    }
    .photo {
      border: 5px solid #d6e3ff;
      img {
        width: 20em;
      }
    }
  }
  .leftSide {
    width: 60%;
    margin: 1em 1em;
    .name {
      font-size: 1.5em;
    }
    .about {
      margin-top: 0.5em;
      color: #2b2a33;
    }
    .socials {
      margin-top: 1em;
      display: flex;
      width: inherit;
      height: 5em;
      & > * {
        width: 6em;
        display: flex;
        padding: 2em;
        border: 2px solid #c9dbff;
        background-color: #d6e3ff;
        text-align: center;
        justify-content: center;
        align-items: center;
        color: #2b2a33;
      }
    }
    .posts {
      margin-top: 5em;
    }
  }
}
