.post {
  margin-top: 2em;
  width: 100%;
  border: 5px solid #c9dbff;
  border-radius: 5px;
  padding: 1em;
  .date {
    font-size: 0.8em;
    color: #527196;
  }
  .title {
    font-size: 2em;
  }
  .content {
    color: #2b2a33;
    line-height: 1.4em;
  }
  .footer {
    margin-top: 1em;
    display: flex;
    justify-content: space-between;
    .buttons {
      font-size: 1.2em;
      display: flex;
      & > * {
        margin-left: 1em;
      }
    }
  }
}
